<template>
  <div class="home">
    <van-search
      v-model="keyword"
      placeholder="通过药店名/药店手机号码搜索"
      show-action
      shape="round"
      @search="onSearch"
      @clear="kill"
      @blur="blur"
    >
      <div slot="action" @click="onSearch">搜索</div>
    </van-search>
    <van-cell-group>
      <van-field
        class="text1"
        value="手机号码"
        label="药店名称"
        left-icon="friends-o"
        readonly
        label-width="120"
        input-align="center"
      >
        <van-button
          slot="button"
          size="small"
          type="primary"
          disabled
          class="btn"
          >审核状态</van-button
        >
      </van-field>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        :offset="30"
        @load="onLoad"
      >
        <div
          @click="toDetail(index)"
          v-for="(item, index) in list"
          :key="index"
        >
          <van-field
            class="field"
            :value="item.phone"
            :label="item.company"
            left-icon="contact"
            label-width="120"
            readonly
            clickable
            input-align="center"
          >
            <van-button slot="button" size="small" type="primary" disabled>{{
              formatState(item.status)
            }}</van-button>
          </van-field>
        </div>
      </van-list>
    </van-cell-group>
    <button
      :class="'button' + ' ' + hoverBtn"
      type="danger"
      block
      @click.prevent="back"
      @touchmove.prevent="touchmove"
      @touchend="touchend"
      ref="button"
    >
      退出
    </button>
    <!-- <div class="bottom"></div> -->
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      keyword: "",
      list: [],
      zt: "",
      Info: {
        phone: "",
        name: "",
        address: "",
        status: "",
        bd: "",
        total: 0,
        pageNo: 1,
        pageSize: 10
      },
      storeInfo: {},
      loading: false,
      finished: false,
      hoverBtn: "",
      mask: true
    };
  },
  mounted() {
    this.storeInfo = this.$getBDUser();
    this.getInfo();
  },
  methods: {
    kill() {
      this.Info.phone = "";
      this.Info.name = "";
    },
    formatState(status) {
      let text = "";
      if ((status & 128) > 0) {
        text = "待审核";
      } else if ((status & 256) > 0) {
        text = "审核通过";
      } else if ((status & 512) > 0) {
        text = "不通过";
      }
      return text;
    },
    getInfo() {
      if (this.mask) {
        this.mask = false;
        JNB.query({
          serverName: "userServer",
          cls: "BackGroundProxyMoudule",
          method: "queryStores",
          params: {
            cname: this.Info.name,
            uphone: this.Info.phone,
            ckstatus:
              this.Info.status === ""
                ? (this.Info.status = -1)
                : this.Info.status,
            address: this.Info.address,
            puid: this.storeInfo.userId,
            mroleid: this.storeInfo.roleCode,
            bd: this.Info.bd === "" ? (this.Info.bd = 0) : this.Info.bd,
            pageNo: this.Info.pageNo,
            pageSize: this.Info.pageSize,
            createdate: ""
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.list = this.list.concat(result.data);
              this.Info.pageNo++;
              if (
                this.Info.pageNo > Math.ceil(result.total / result.pageSize)
              ) {
                this.finished = true;
                this.Info.pageNo = 1;
              }
              this.loading = false;
              this.mask = true;
            }
          }
        });
      }
    },
    onSearch() {
      this.Info.phone = "";
      this.Info.name = "";
      if (this.keyword.length === 11) {
        this.Info.phone = this.keyword;
      } else {
        this.Info.name = this.keyword;
      }
      this.list = [];
      this.Info.pageNo = 1;
      this.finished = false;
      this.getInfo();
    },
    toDetail(i) {
      this.$router.push({
        path: "/bdDetail"
      });
      sessionStorage.setItem("storeInfo", JSON.stringify(this.list[i]));
    },
    back() {
      Dialog.confirm({
        title: "温馨提示",
        message: "确认要退出吗？"
      })
        .then(() => {
          JNB.query({
            serverName: "userServer",
            cls: "LoginRegistrationModule",
            method: "logout",
            callback: data => {
              let result = JSON.parse(data);
              if (result.code == 200) {
                this.$delBDUser();
                this.$toPage("home");
              }
            }
          });
        })
        .catch(() => {});
    },
    blur() {
      if (this.keyword == "") {
        this.Info.phone = "";
        this.Info.name = "";
        this.list = [];
        this.getInfo();
      }
    },
    onLoad() {
      this.getInfo();
    },
    touchmove(e) {
      this.hoverBtn = "hoverBtn";
      let scrollWidth = document.documentElement.clientWidth;
      let scrollHeight = document.documentElement.clientHeight;
      this.$refs.button.style.position = "fixed";
      if (e.changedTouches[0].clientX - 25 <= 0) {
        this.$refs.button.style.left = 0;
      } else if (e.changedTouches[0].clientX - 25 >= scrollWidth - 50) {
        this.$refs.button.style.left = scrollWidth - 50 + "px";
      } else {
        this.$refs.button.style.left = e.changedTouches[0].clientX - 25 + "px";
      }
      if (e.changedTouches[0].clientY - 100 <= 0) {
        this.$refs.button.style.top = 55 + "px";
      } else if (e.changedTouches[0].clientY - 25 >= scrollHeight - 50) {
        this.$refs.button.style.top = scrollHeight - 50 + "px";
      } else {
        this.$refs.button.style.top = e.changedTouches[0].clientY - 25 + "px";
      }
    },
    touchend() {
      this.hoverBtn = "";
    }
  }
};
</script>
<style scoped>
.home .van-search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}
.home .van-cell-group .text1 {
  font-size: 32px;
  line-height: 60px;
}
.home .van-cell-group .text1 .van-button {
  background-color: transparent;
  border: 0px;
  color: #000;
  font-size: 30px;
}
.home .van-cell-group .field {
  font-size: 28px;
  line-height: 60px;
  color: #808080;
  border-bottom: 1px solid #f5f5f5;
}
.home .van-cell-group .field .van-button {
  background-color: transparent;
  border: 0px;
  color: #f91e1e;
  font-size: 28px;
  width: 160px;
}
.home .button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 88px;
  height: 88px;
  border-radius: 50%;
  background-color: #000;
  opacity: 0.2;
  text-align: center;
  line-height: 88px;
  color: #fff;
  font-size: 24px;
  border: 1px solid #000;
  box-shadow: 0 0 35px 5px #000;
}
.home .hoverBtn {
  box-shadow: 0 0 55px 25px #ff6700;
  border: 1px solid #ff6700;
  background-color: #ff6700;
}
.home {
  padding: 100px 0 0;
  height: 100vh;
  box-sizing: border-box;
  background-color: #fff;
}
</style>